/**
 * Internal dependencies
 */
import markdown from './markdown';
import codeEditor from './code-editor';
import disableTitle from './disable-title';
import headingLabel from './heading-label';
import clearFormating from './clear-formatting';
var registerPlugin = wp.plugins.registerPlugin;
export default function registerWCAPlugins() {
  [markdown, codeEditor, disableTitle, headingLabel, clearFormating].forEach(function (block) {
    if (!block) return;
    var name = block.name,
        render = block.render;
    registerPlugin(name, {
      icon: false,
      render: render
    });
  });
}
registerWCAPlugins();